import { onlyNumbers } from '@brazilian-utils/helper-only-numbers';

function numberToWeightArray(weight, length) {
  var array = [];

  for (var i = 0; i < length; i++) {
    array.push(weight - i);
  }

  return array;
}

function createChecksum(str, weights) {
  return str.split('').reduce(function (acc, value, index) {
    return acc + parseInt(value, 10) * weights[index];
  }, 0);
}

function generateChecksum(base, weights) {
  var number = onlyNumbers(base);

  if (typeof weights === 'number') {
    return createChecksum(number, numberToWeightArray(weights, number.length));
  }

  if (Array.isArray(weights)) {
    return createChecksum(number, weights);
  }

  throw new Error('Invalid weight type. Should be an Array like or a Number');
}

export { generateChecksum };